
#header {
  height: 40px;
  background: #ccc;
  overflow: hidden;
}
#header .logo {
  float: left;
}
#header .logo img {
  height: 40px;
}
#header ol {
  overflow: hidden;
  float: right;
  padding: 0 5px;
}
#header ol li {
  width: 40px;
  height: 1px;
  background: #333;
  margin-bottom: 10px;
}



